.login__input {
    width: 354px;
    margin: 0 0 23px;
    outline: none;
    border: none;
    background: none;
    border-bottom: 2px solid #ccc;
    color: #fff;
    font-size: 14px;
    line-height: 30px;
  }
  
  .login__input:last-of-type {
    margin: 0;
  }
  
  @media screen and (max-width: 642px) {
    .login__input {
      width: 260px;
      margin: 0 0 14.5px;
  
    }
  
    .login__input:last-of-type {
      margin-top: 1px;
    }
  }